console.log('hello world')
const images = require('./images/hovers/*.*');
console.log(images);

window.onload = () => {

    new ImageHoverEffect();
}

class ImageHoverEffect {

    constructor() {

        this.elements = document.querySelectorAll('[data-hover]');
        this.image = document.createElement('img');
        this.image.className = "imageHoverEffekt";
        this.imageWidth = 440;
        this.imageHeight = 580;
        this.elements.forEach(item => {
            console.log(item.dataset.hover);
            item.addEventListener('mouseenter', this.hoverStart.bind(this));
            item.addEventListener('mouseleave', this.hoverEnd.bind(this));
            item.addEventListener('mousemove', this.hoverMove.bind(this));
        });
    }

    hoverStart(e) {
        const current = e.target;

        if(!current.contains(this.image)) {
            first = Object.keys(images[current.dataset.hover])
            this.image.src = images[current.dataset.hover][first];
            current.appendChild(this.image);
            this.image.addEventListener('load', () => {
                this.image.classList.add('added');
            })
        }
    }
    hoverEnd(e) {
        this.image.classList.remove('added');
        this.image.remove();
    }
    hoverMove(e) {
        let rect = e.target.getBoundingClientRect(),
            x = e.clientX - rect.left,
            y = e.clientY - rect.top;
            
        this.image.style.top = (y - (this.imageHeight / 2)) +"px";
        this.image.style.left = (x - (this.imageWidth / 2)) + "px";
    }
}
